

.task {
    padding: 1.5em;
    .done{
      text-decoration: line-through;
    }
    &-group {
      display: flex;
    justify-content: space-between;
    span{
      cursor: pointer;
      padding: 5px;
      color: red;
    }
  }
  &-date{
    text-align: start;
  }
}
.task-done{
  border-left: 5px solid darkred;
  
}